import React, { useState, useEffect } from 'react';
import { Link, NavLink } from 'react-router-dom';
import { useAuth } from '../../contexts/AuthContext';
import { Bars3Icon, XMarkIcon, UserIcon, AcademicCapIcon, ChatBubbleLeftRightIcon, Cog6ToothIcon, BookmarkIcon, InformationCircleIcon, HomeIcon, BookOpenIcon, HeartIcon, ArrowRightOnRectangleIcon } from '@heroicons/react/24/outline';
import LoginModal from '../auth/LoginModal';

const Navbar = () => {
  const { user, logout } = useAuth();
  const [isOpen, setIsOpen] = useState(false);
  const [isLoginModalOpen, setIsLoginModalOpen] = useState(false);

  // Block scroll when mobile menu is open
  useEffect(() => {
    if (isOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'unset';
    }

    // Cleanup on unmount
    return () => {
      document.body.style.overflow = 'unset';
    };
  }, [isOpen]);

  const activeLinkStyle = {
    backgroundColor: '#fef3c7',
    color: '#92400e',
    fontWeight: '600',
  };

  const authLinks = user ? [
    { to: '/dashboard', text: 'Panel główny', icon: Cog6ToothIcon },
    { to: '/favorites', text: 'Moje zakładki', icon: BookmarkIcon },
    { to: '/profile', text: 'Profil', icon: UserIcon },
    { to: '/messages', text: 'Wiadomości', icon: ChatBubbleLeftRightIcon },
    ...(user.role === 'teacher' ? [{ to: '/applications', text: 'Aplikacje', icon: AcademicCapIcon }] : []),
    ...(user.role === 'student' ? [{ to: '/my-applications', text: 'Moje aplikacje', icon: AcademicCapIcon }] : []),
  ] : [
    { to: '/otp-login', text: 'Zaloguj się', icon: UserIcon },
    { to: '/otp-info', text: 'Jak to działa?', icon: InformationCircleIcon },
    { to: '/otp-test', text: 'Test OTP', icon: AcademicCapIcon },
  ];

  const handleLogout = () => {
    logout();
    setIsOpen(false);
  };

  const toggleMenu = () => {
    setIsOpen(!isOpen);
  };

  const openLoginModal = () => {
    setIsLoginModalOpen(true);
    setIsOpen(false); // Close mobile menu if open
  };

  const closeLoginModal = () => {
    setIsLoginModalOpen(false);
  };

  return (
    <>
      <nav className="bg-white border-b border-[#E4E4E4] sticky top-0 z-50">
        <div className="max-w-7xl mx-auto px-2 sm:px-4 lg:px-8">
          <div className="flex items-center justify-between" style={{height: '54px'}}>
            
            {/* Logo */}
            <div className="flex items-center">
              <Link to="/" className="text-2xl font-bold hover:text-blue-600 transition-colors duration-200" style={{color: '#333333'}}>
                korke
              </Link>
            </div>

            {/* Desktop Navigation */}
            <div className="hidden md:block">
              <div className="ml-10 flex items-baseline space-x-4">
                <div className="ml-4">
                  {user ? (
                     <div className="flex items-center space-x-4">
                        <NavLink to="/dashboard" style={({ isActive }) => isActive ? activeLinkStyle : undefined} className="text-gray-600 hover:text-gray-900 hover:bg-amber-50 px-3 py-2 rounded-lg text-sm font-medium transition-all duration-200">Panel główny</NavLink>
                        <button onClick={logout} className="bg-blue-600 text-white px-4 py-2 rounded-lg text-sm font-medium hover:bg-blue-700 transition-colors duration-200 shadow-sm">Wyloguj</button>
                     </div>
                  ) : (
                    <div className="flex items-center space-x-2">
                      <button 
                        onClick={openLoginModal}
                        className="bg-blue-600 text-white px-4 py-2 rounded-lg text-sm font-medium hover:bg-blue-700 transition-colors duration-200 shadow-sm"
                      >
                        Zaloguj się
                      </button>
                    </div>
                  )}
                </div>
              </div>
            </div>

            {/* Mobile menu button */}
            <div className="md:hidden">
              <button
                onClick={toggleMenu}
                type="button"
                className="p-2 rounded-lg text-gray-600 hover:text-gray-900 focus:outline-none transition-all duration-200"
                aria-controls="mobile-menu"
                aria-expanded="false"
              >
                <span className="sr-only">{isOpen ? 'Zamknij menu' : 'Otwórz menu główne'}</span>
                {isOpen ? (
                  <XMarkIcon className="h-6 w-6" />
                ) : (
                  <Bars3Icon className="h-6 w-6" />
                )}
              </button>
            </div>
          </div>
        </div>
      </nav>
      
      {/* Mobile menu overlay */}
      {isOpen && (
        <div className="fixed inset-0 bg-black bg-opacity-50 z-[9998] lg:hidden" onClick={toggleMenu} />
      )}

      {/* Menu panel - full screen with own navbar */}
      <div 
        className={`fixed inset-0 bg-white z-[9999] lg:hidden transform transition-transform duration-300 ease-in-out ${
          isOpen ? 'translate-x-0' : 'translate-x-full'
        }`}
      >
        {/* Menu navbar */}
        <div className="flex items-center justify-between px-4 py-3 border-b border-gray-200 h-[54px]">
          <h2 className="text-lg font-semibold text-gray-800">Menu</h2>
          <button
            onClick={toggleMenu}
            className="p-2 text-gray-600 hover:text-gray-800 transition-colors"
          >
            <XMarkIcon className="h-6 w-6" />
          </button>
        </div>

        {/* Menu content */}
        <div className="flex-1 overflow-y-auto">
          <nav className="px-4 py-6">
            <div className="space-y-4">
              <Link
                to="/"
                className="flex items-center px-3 py-2 text-gray-700 hover:bg-gray-100 rounded-lg transition-colors"
                onClick={toggleMenu}
              >
                <HomeIcon className="h-5 w-5 mr-3" />
                Strona główna
              </Link>
              
              <Link
                to="/teachers"
                className="flex items-center px-3 py-2 text-gray-700 hover:bg-gray-100 rounded-lg transition-colors"
                onClick={toggleMenu}
              >
                <AcademicCapIcon className="h-5 w-5 mr-3" />
                Nauczyciele
              </Link>

              <Link
                to="/tutoring"
                className="flex items-center px-3 py-2 text-gray-700 hover:bg-gray-100 rounded-lg transition-colors"
                onClick={toggleMenu}
              >
                <BookOpenIcon className="h-5 w-5 mr-3" />
                Korepetycje
              </Link>

              {user ? (
                <>
                  <Link
                    to="/dashboard"
                    className="flex items-center px-3 py-2 text-gray-700 hover:bg-gray-100 rounded-lg transition-colors"
                    onClick={toggleMenu}
                  >
                    <UserIcon className="h-5 w-5 mr-3" />
                    Panel użytkownika
                  </Link>
                  
                  <Link
                    to="/profile"
                    className="flex items-center px-3 py-2 text-gray-700 hover:bg-gray-100 rounded-lg transition-colors"
                    onClick={toggleMenu}
                  >
                    <Cog6ToothIcon className="h-5 w-5 mr-3" />
                    Profil
                  </Link>
                  
                  <Link
                    to="/messages"
                    className="flex items-center px-3 py-2 text-gray-700 hover:bg-gray-100 rounded-lg transition-colors"
                    onClick={toggleMenu}
                  >
                    <ChatBubbleLeftRightIcon className="h-5 w-5 mr-3" />
                    Wiadomości
                  </Link>

                  <Link
                    to="/favorites"
                    className="flex items-center px-3 py-2 text-gray-700 hover:bg-gray-100 rounded-lg transition-colors"
                    onClick={toggleMenu}
                  >
                    <HeartIcon className="h-5 w-5 mr-3" />
                    Ulubione
                  </Link>

                  {user.role === 'teacher' && (
                    <Link
                      to="/applications"
                      className="flex items-center px-3 py-2 text-gray-700 hover:bg-gray-100 rounded-lg transition-colors"
                      onClick={toggleMenu}
                    >
                      <AcademicCapIcon className="h-5 w-5 mr-3" />
                      Aplikacje
                    </Link>
                  )}

                  {user.role === 'student' && (
                    <Link
                      to="/my-applications"
                      className="flex items-center px-3 py-2 text-gray-700 hover:bg-gray-100 rounded-lg transition-colors"
                      onClick={toggleMenu}
                    >
                      <AcademicCapIcon className="h-5 w-5 mr-3" />
                      Moje aplikacje
                    </Link>
                  )}

                  <button
                    onClick={() => {
                      logout();
                      toggleMenu();
                    }}
                    className="flex items-center w-full px-3 py-2 text-gray-700 hover:bg-gray-100 rounded-lg transition-colors"
                  >
                    <ArrowRightOnRectangleIcon className="h-5 w-5 mr-3" />
                    Wyloguj się
                  </button>
                </>
              ) : (
                <>
                  <button
                    onClick={openLoginModal}
                    className="flex items-center w-full px-3 py-2 text-gray-700 hover:bg-gray-100 rounded-lg transition-colors"
                  >
                    <UserIcon className="h-5 w-5 mr-3" />
                    Zaloguj się
                  </button>
                  
                  <Link
                    to="/otp-info"
                    className="flex items-center px-3 py-2 text-gray-700 hover:bg-gray-100 rounded-lg transition-colors"
                    onClick={toggleMenu}
                  >
                    <InformationCircleIcon className="h-5 w-5 mr-3" />
                    Jak to działa?
                  </Link>
                  
                  <Link
                    to="/otp-test"
                    className="flex items-center px-3 py-2 text-gray-700 hover:bg-gray-100 rounded-lg transition-colors"
                    onClick={toggleMenu}
                  >
                    <AcademicCapIcon className="h-5 w-5 mr-3" />
                    Test OTP
                  </Link>
                </>
              )}
            </div>
          </nav>
        </div>
      </div>

      {/* Login Modal */}
      <LoginModal isOpen={isLoginModalOpen} onClose={closeLoginModal} />
    </>
  );
};

export default Navbar; 